import React, { Component } from 'react';
import './App.css';
import styled from 'styled-components';
import { Tag, TagView } from './types';
import TagControl from './TagControl';
import axios, { AxiosResponse } from 'axios';

const api = 'AIzaSyAoPhC6XM-ZyIZ1dRSSXWvpJ9gpd3Y_nZI';

const clientId = 'UUpuV6h9dIb1Cq4nIcFapvfg';

interface Props {}

interface State {
  videos: BarringtonVideo[];
  tags: Tag[];
  selectedTagIds: ReadonlyArray<string>;
}

interface BarringtonVideo {
  id: string;
  tagIds: ReadonlyArray<string>;
  description?: string;
}

const MainContainer = styled.section`
  display: grid;
  align-items: center;
  justify-content: center;
  grid-gap: 0.5em;
  grid-template-rows: repeat(3, auto) 1fr;
  grid-template-areas:
    'MainTitle'
    'TagContainer'
    'VideosTitle'
    'VideoContainer';
  width: 100%;
  height: 100%;
`;

const HeaderText = styled.h4`
  color: ${props => props.theme.lightColor};
  margin: 0em 0.5em;
  letter-spacing: 2px;
`;

const MainTitle = styled.h4`
  grid-area: MainTitle;
  color: ${props => props.theme.lightColor};
  letter-spacing: 2px;
  text-transform: uppercase;
`;

const VideosTitle = styled.h4`
  grid-area: VideosTitle;
  color: ${props => props.theme.lightColor};
  margin: 0em 0.5em;
  letter-spacing: 2px;
  text-transform: uppercase;
`;

const TagContainer = styled.section`
  grid-area: TagContainer;
  display: flex;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
  border: 0px solid;
  padding: 0.5em;
`;

const VideoContainer = styled.section`
  grid-area: VideoContainer;
  display: grid;
  align-self: center;
  align-items: flex-start;
  grid-template-columns: repeat(4, 300px);
  grid-auto-rows: 200px;
  grid-gap: 0.5em;
  border: 0px solid;

  @media (max-width: 720px) {
    grid-template-columns: auto;
  }
`;

const IFrame = styled.iframe`
  border: none;
  justify-self: center;
`;

const Video = ({ id }: BarringtonVideo) => (
  <IFrame
    key={id}
    title={id}
    src={`https://www.youtube.com/embed/${id}?controls=0`}
  />
);

const renderTag = (onClick: (id: string) => void) => (t: TagView) => (
  <TagControl
    key={t.id}
    onClick={onClick}
    confirmDeleteText="Are you sure you want to remove tag?"
    tag={t}
  />
);

const createTagView = (tagIds: ReadonlyArray<string>) => (t: Tag) => ({
  ...t,
  isSelected: tagIds.includes(t.id),
  allowDelete: false,
  allowSelect: false
});

const getFilteredVideos = (selectedTagIds: ReadonlyArray<string>) => (
  vid: BarringtonVideo
) =>
  vid.tagIds.length === 0
    ? false
    : selectedTagIds.length > 0
    ? vid.tagIds.some(t => selectedTagIds.includes(t))
    : true;

class App extends Component<{}, State> {
  public state: Readonly<State> = {
    videos: [
      {
        id: 'e_7Kf_AtLWA',
        description: 'George 20 yard recption',
        tagIds: ['6', '7']
      },
      {
        id: 'gL2hHgDW9cw',
        description: 'Lino 30 yard reception',
        tagIds: ['1', '3']
      },
      {
        id: 'Xv15pESvRjE',
        description: 'Lino interception',
        tagIds: ['1']
      },
      {
        id: 'SDncseDqgbI',
        description: 'Margo 10 yard run',
        tagIds: ['4']
      }
    ],
    selectedTagIds: [],
    tags: [
      { id: 'Lino', name: 'Lino' },
      { id: 'Mark', name: 'Mark' },
      { id: 'Nolin', name: 'Nolin' },
      { id: 'Margo', name: 'Margo' },
      { id: 'Luke', name: 'Luke' },
      { id: 'George', name: 'George' },
      { id: 'Charlie', name: 'Charlie' }
    ]
  };

  public componentDidMount = async () => {
    const resp: AxiosResponse<YouTubeChannelData> = await axios.get(
      `https://www.googleapis.com/youtube/v3/playlistItems?&part=snippet&maxResults=50&playlistId=${clientId}&key=${api}`
    );
    // console.dir(resp.data);

    const videos: BarringtonVideo[] = resp.data.items.map(x => ({
      id: x.snippet.resourceId.videoId,
      description: x.snippet.title,
      tagIds: []
    }));

    const tagVideos = await Promise.all(
      videos.map(async (x: BarringtonVideo) => {
        const r = await axios.get(
          `https://www.googleapis.com/youtube/v3/videos?&part=snippet&id=${
            x.id
          }&key=${api}`
        );

        const snip = r.data.items[0].snippet;
        return (await snip.tags) ? { ...x, tagIds: snip.tags } : x;
      })
    );
    // console.dir(tagVideos);

    this.setState({ videos: tagVideos });
  };

  public handleTagSelection = (id: string) =>
    this.setState(prevState => ({
      selectedTagIds: prevState.selectedTagIds.includes(id)
        ? prevState.selectedTagIds.filter(p => p !== id)
        : [...prevState.selectedTagIds, id]
    }));

  render() {
    const { selectedTagIds, videos, tags } = this.state;
    const filteredVideos = videos.filter(getFilteredVideos(selectedTagIds));

    return (
      <MainContainer className="App">
        <MainTitle>Coach Lewis' Teams Highlights</MainTitle>
        <TagContainer>
          <HeaderText>TAGS</HeaderText>
          {tags
            .map(createTagView(selectedTagIds))
            .map(renderTag(this.handleTagSelection))}
        </TagContainer>
        <VideosTitle>Videos: {filteredVideos.length}</VideosTitle>
        <VideoContainer>{filteredVideos.map(Video)}</VideoContainer>
      </MainContainer>
    );
  }
}

export default App;

interface YouTubeChannelData {
  kind: string;
  etag: string;
  nextPageToken: string;
  pageInfo: PageInfo;
  items: Item[];
}

interface Item {
  kind: string;
  etag: string;
  id: string;
  snippet: Snippet;
}

interface Snippet {
  publishedAt: string;
  channelId: string;
  title: string;
  description: string;
  thumbnails: Thumbnails;
  channelTitle: string;
  playlistId: string;
  position: number;
  resourceId: ResourceId;
}

interface ResourceId {
  kind: string;
  videoId: string;
}

interface Thumbnails {
  default: Default;
  medium: Default;
  high: Default;
  standard: Default;
  maxres: Default;
}

interface Default {
  url: string;
  width: number;
  height: number;
}

interface PageInfo {
  totalResults: number;
  resultsPerPage: number;
}

interface YouTubeVideo {
  kind: string;
  etag: string;
  pageInfo: PageInfo;
  items: Item[];
}

interface Item {
  kind: string;
  etag: string;
  id: string;
  snippet: Snippet;
}

interface Snippet {
  publishedAt: string;
  channelId: string;
  title: string;
  description: string;
  thumbnails: Thumbnails;
  channelTitle: string;
  tags: string[];
  categoryId: string;
  liveBroadcastContent: string;
  localized: Localized;
}

interface Localized {
  title: string;
  description: string;
}

interface Thumbnails {
  default: Default;
  medium: Default;
  high: Default;
  standard: Default;
  maxres: Default;
}

interface Default {
  url: string;
  width: number;
  height: number;
}

interface PageInfo {
  totalResults: number;
  resultsPerPage: number;
}
