import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  DefaultButton,
  Dialog,
  DialogFooter,
  DialogType,
  PrimaryButton
} from 'office-ui-fabric-react';
import React from 'react';
import styled from './styled-components';
import ThemeInterface from './theme';
import { TagView } from './types';
import { fadeIn } from './animations';
// import posed from 'react-pose';

interface Props {
  className?: string;
  delay?: number;
  theme?: ThemeInterface;
  tag: TagView;
  confirmDeleteText: string;
  onClick?: (id: string) => void;
  onDelete?: (id: string) => void;
}

interface State {
  dialogIsHidden?: boolean;
}

const MainContainer = styled.li`
  display: flex;
  justify-content: space-between;
  margin: 4px 2px 0 0;
  font-size: 0.8em;
  font-weight: bold;
  color: ${(props: Props) =>
    props.tag.isSelected ? props.theme && props.theme.darkColor : 'darkgray'};
  align-items: center;
  cursor: ${props => (props.tag.allowSelect ? 'pointer' : 'default')};
  /* cursor: ${props => (props.tag.isSelected ? 'default' : 'pointer')}; */
  border: 3px solid ${props =>
    props.tag.isSelected ? props.theme.darkColor : 'darkgray'};
  border-radius: 1.5em;
  animation: ${fadeIn} 0.3s ${props => props.delay || 0}s ease-in-out
    both;
  padding: 5px 9px;
  transition: all 0.3s ease;
  :hover {
    color: ${props => props.tag.allowSelect && props.theme.darkColor};
    border-color: ${props => props.tag.allowSelect && props.theme.darkColor};
  }
`;

// enum Animation {
//   'fadeIn' = 'fadeIn',
// }

// const Item = posed.li({
//   enter: { opacity: 1 },
//   exit: { opacity: 0 },
// });

const CloseContainer = styled.section`
  :hover {
    cursor: pointer;
  }
`;

export default class TagControl extends React.PureComponent<Props, State> {
  public state: Readonly<State> = { dialogIsHidden: true };

  public handleDelete = () => {
    this.props.onDelete && this.props.onDelete(this.props.tag.id);
    this.handleCloseDeleteDialog();
  };

  public handleShowDeleteDialog = () => {
    this.setState({ dialogIsHidden: false });
  };
  public handleCloseDeleteDialog = () => {
    this.setState({ dialogIsHidden: true });
  };

  public render() {
    const { confirmDeleteText, onClick, onDelete, tag, theme } = this.props;
    return (
      <MainContainer
        confirmDeleteText={confirmDeleteText}
        tag={tag}
        key={tag.id}
        onClick={() => onClick && onClick(tag.id)}
      >
        {tag.name}
        {onDelete && (
          <CloseContainer
            onClick={() => onDelete && this.handleShowDeleteDialog()}
          >
            <FontAwesomeIcon style={{ marginLeft: 5 }} icon="times" />
          </CloseContainer>
        )}
        <Dialog
          hidden={this.state.dialogIsHidden}
          onDismiss={() => this.handleCloseDeleteDialog()}
          dialogContentProps={{
            type: DialogType.normal,
            title: 'Delete Tag',
            subText: confirmDeleteText
          }}
          modalProps={{
            titleAriaId: 'myLabelId',
            subtitleAriaId: 'mySubTextId',
            isBlocking: false,
            containerClassName: 'ms-dialogMainOverride'
          }}
        >
          {
            null /** You can also include null values as the result of conditionals */
          }
          <DialogFooter>
            <PrimaryButton
              style={{ background: theme && theme.darkColor }}
              onClick={() => this.handleDelete()}
              text="Yes"
            />
            <DefaultButton
              onClick={() => this.handleCloseDeleteDialog()}
              text="No"
            />
          </DialogFooter>
        </Dialog>
      </MainContainer>
    );
  }
}
