import { keyframes } from 'styled-components';

export const ping = keyframes` 
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.3;
  }

`;

export const expand = keyframes` 
   0% {
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    filter: blur(0px);
    opacity: 1;
  }
`;

export const fadeIn = keyframes` 
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
`;

export const fadeOut = keyframes`
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
`;

export const fadeInFwd = keyframes`
  0% {
    -webkit-transform: translateZ(-80px);
            transform: translateZ(-80px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    opacity: 1;
  }
`;

export const swingIn = keyframes`
  0% {
    -webkit-transform: rotateX(-100deg);
            transform: rotateX(-100deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    opacity: 0;
  }
  100% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    opacity: 1;
  }
`;

export const swingOut = keyframes`
  0% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    opacity: 1;
  }
  100% {
    -webkit-transform: rotateX(-100deg);
            transform: rotateX(-100deg);
    -webkit-transform-origin: top;
            transform-origin: top;
    opacity: 0;
  }
`;

export const slideInRight = keyframes`
    /* from {
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
    opacity: 0;
  } */
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
`;

export const slideOutRight = keyframes`
   /* from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  } */
  to {
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
    opacity: 0;
  }
`;

export const slideIn = keyframes`
       from {
        - webkit - transform: translateX(1000px);
                transform: translateX(1000px);
                opacity: 0;
  }
        to {
        - webkit - transform: translateX(0);
        transform: translateX(0);
        opacity: 1;
  }`;

export const slideOut = keyframes`
   from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
  to {
    -webkit-transform: translateX(1000px);
            transform: translateX(1000px);
    opacity: 0;
  }
`;

export const slideInRotated = keyframes`
       0% {
        -webkit-transform: translateX(1000px) rotate(90deg);
                transform: translateX(1000px) rotate(90deg);
                opacity: 0;
  }
        100% {
        -webkit-transform: translateX(-30px) rotate(90deg);
        transform: translateX(-30px) rotate(90deg);
        opacity: 1;
  }`;

export const slideOutRotated = keyframes`
   0% {
    -webkit-transform: translateX(-30);
            transform: translateX(-30) rotate(90deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(1000px);
            transform: translateX(1000px) rotate(90deg);
    opacity: 0;
  }
`;

